	import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
    import switchc from '@/components/zz-switchc/zz-switchc.vue'
	var timer;
	export default{
		data(){
			return{
				phone: '',
				email: '',
				password: '',
				second: 0
			}
		},
		components:{
			uniNavBar,
			switchc
		},
		computed:{
			yanzhengma(){
				if(this.second==0){
					return '获取验证码';
				}else{
					return this.second+'秒';
				}
			}
		},
		methods:{
			switchchange(e) {
				console.log(e);
			},
			getcode(){
				var that = this;
				if(that.second>0){
					return;
				} 
				uni.request({
					url: '', //仅为示例，并非真实接口地址。
					data: {content:that.account},
					method: 'GET',
					dataType:'json',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (res) => {  
						if(res.data.code == 1){
							uni.showToast({title:res.data.msg,icon:'none'});
							that.second = 0;
						}else{
							uni.showToast({title:res.data.msg});
							that.second = 60;
							timer = setInterval(function(){
								that.second--;
								if(that.second==0){
									clearInterval(timer)
								}
							},1000)
						}
					}
				});
			}
		}
	}
